body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: black !important;
}

.theme-gradient-text {
  background: -webkit-linear-gradient(290deg, #6459e3 50.06%, #baa7ed 88.46%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.black-gradient-text {
  background: -webkit-linear-gradient(
    270deg,
    #ffffff 46.37%,
    rgba(51, 51, 51, 0.69) 131.5%
  );
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.theme-box-shadow {
  box-shadow: 1px -1px #ffffff30;
}

.bg-image {
  background-image: url('../src/static/vectors/top-light.png'),
    url('../src/static/vectors/footer-deep-waves.png');
  background-position:
    top center,
    bottom center;
  background-repeat: no-repeat;
  background-size: contain;
}
